}



// Адаптив мобильная версия
@media (max-width:789px) {
    body.lock {
        overflow: hidden;
    }

    .header {
        position: fixed;
        height: 56px;

        .wrapper {
            width: 100%;

            .header__content {
                border: none;

                .header__logo {

                    padding-top: 21px;

                }

                .header__logo a svg {
                    width: 102px;
                }

                .header__burger {
                    display: block;
                    position: relative;
                    width: 24px;
                    height: 18px;
                    z-index: 3;
                }

                .header__burger span {
                    background-color: #FFFFFF;
                    position: absolute;
                    width: 100%;
                    height: 2px;
                    left: 0;
                    top: 8px;
                    transition: all 0.3s ease 0s;
                }

                .header__burger::before,
                .header__burger::after {
                    content: '';
                    background-color: #FFFFFF;
                    position: absolute;
                    width: 100%;
                    height: 2px;
                    left: 0;
                    transition: all 0.3s ease 0s;
                }

                .header__burger::before {
                    top: 0;
                }

                .header__burger::after {
                    bottom: 0;
                }

                .header__burger.active::before {
                    transform: rotate(45deg);
                    top: 8px;
                }

                .header__burger.active::after {
                    transform: rotate(-45deg);
                    bottom: 8px;
                }

                .header__burger.active span {
                    transform: scale(0);
                }

                .header__menu {
                    position: fixed;
                    top: -100%;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    overflow: auto;
                    transition: all 0.3s ease 0s;
                    background-color: transparent;
                    padding: 70px 0 0 0;
                    /*position: relative;*/
                    z-index: 1;
                    background: url(../img/img_table.png) no-repeat center/cover;
                    overflow: hidden;

                    .header__list {
                        display: block;
                        width: 100%;
                        position: relative;
                        z-index: 2;
                        margin-top: 50px;
                        padding-left: 15px;
li{
    .header__item {
        display: block;
        margin: 0 auto;
        width: 100%;
        text-align: start;
        font-size: 13px;
        height: 63px;


    }
    .header__item:hover {
        border: none;
    }

    .header__item:active {
        border: none;
    }
}


                    }
                }

                .header__menu::after {
                    content: "";
                    position: absolute;
                    z-index: 0;
                    width: 100%;
                    height: 100%;
                    top: 0;
                    left: 0;
                    max-width: auto;
                    background: rgba(37, 43, 73, 0.8) no-repeat center/cover;
                }

                .header__menu.active {
                    top: 0;
                }
            }
        }
    }

    .features {
        .grid__features {
            grid-template: 1fr 1fr 1fr 1fr 1fr 1fr/ 1fr;
            padding: 50px 0 35px 0;
            row-gap: 50px;

            .grid__item {
                .icon svg {
                    height: 55px;
                }

                .title {
                    margin: 48px 0 10px 0;
                }
            }
        }
    }



    .grid__work {
        grid-template: 1fr 1fr 1fr 1fr/1fr 1fr;

        .title {
            margin-bottom: 10px;
        }
    }

    .team__content {
        flex-wrap: wrap;
    }

    .testimonials {
        .carousel-indicators [data-bs-target] {
            width: 9.5px;
            height: 9.5px;
            margin: 0 10px;
        }

        #carouselExampleIndicators .carousel-indicators {
            bottom: -50px;
        }
    }

    .testimonials__item {
        display: block;

        .testimonials__photo {
            width: 100%;
        }

        .testimonials__info {
            width: 100%;
            margin: 0 auto;
            padding: 20px 15px 95px 15px;

            .testimonials__idiom {
                text-align: center;
            }

            .testimonials__author {
                display: block;
                text-align: center;
            }
        }
    }

    .banner {
        .banner__title {
