        }

        .banner__subtitle {
            font-size: 14px;
            line-height: 16px;
            padding: 0 0 51px 0;
        }

        .banner__btn {
            padding-bottom: 52px;
        }

        .btn__item {
            height: 60px;
            width: 280px;
        }
    }


    .team__content {
        margin: 35px 0 -14px 0;

        .team__item {
            margin: 0 auto;
        }

        .text {
            margin: 38px 0 55px 0;
        }

        .social {
            margin-bottom: 49px;
        }
    }

    .download__item {
        padding: 67px 0 45px 0;

        .download__title {
            font-size: 18px;
            line-height: 21px;
        }

        .download__subtitle {
            font-size: 13px;
            line-height: 15px;
